  text-align: center;
  font-size: 36px;
}
.product-size-grid
{
  display: grid;
  grid-template-columns: 50fr 1fr 50fr;
  width: 85%;
  column-gap: 50px;
  justify-content: space-between;
  margin: auto;
}
.product-size img{
  position: absolute;
  border-radius: 26px;
  width: 180px;
  border: #583C2E solid 5px;
  border-bottom: #583C2E solid 30px;
  z-index: 5;
  margin-bottom: 0px;
